import { Component, Injectable, Injector, Input, ComponentResolver, 
         ComponentFactory, ComponentRef, ViewContainerRef, DynamicComponentLoader} from '@angular/core';
import { LoadInComponentComponent } from './load-in-component.component'

@Injectable()

export class LoadToComponentService {
    
  constructor(private _componentResolver: ComponentResolver, 
  private _injector: Injector, private _vc:ViewContainerRef,
  private _loader:DynamicComponentLoader) {
  } 
  
  open() {
    this._componentResolver.resolveComponent(LoadInComponentComponent)
      .then((cf: ComponentFactory<any>) => {
          var cRef = cf.create(this._injector);
          console.log(cRef.location.nativeElement);
        //  document.getElementById('overly').appendChild(cRef.location.nativeElement);
          this._loader.loadNextToLocation(LoadInComponentComponent,this._vc);
     });
  }
}